.card__side-image {
    display: grid;
    @include x-column-grid(12);
    grid-auto-flow: column;

    &[data-colour-theme='light'] {
        .card__side-image__text {
            color: $color-gray10;
        }
        a {
            @include anchor-discreet(
                $color-gray10,
                $color-gray10,
                $color-gray10,
                $color-gray10,
                $color-gray10
            );
        }
    }

    &[data-colour-theme='dark'] {
        .card__side-image__text {
            color: $color-purewhite;
        }
        a {
            @include anchor-discreet(
                $color-purewhite,
                $color-purewhite,
                $color-purewhite,
                $color-purewhite,
                $color-purewhite
            );
        }
    }

    figure {
        // Image: left
        &[data-image-position='left'] {
            grid-column: 1/5;
            & + .card__side-image__text {
                grid-column: 5/-1;
            }
            &[data-image-width='wide'] {
                grid-column: 1/7;
                & + .card__side-image__text {
                    grid-column: 7/-1;
                }
            }
        }

        // Image: right
        &[data-image-position='right'] {
            grid-column: -5/-1;
            & + .card__side-image__text {
                grid-column: 1/-5;
            }
            &[data-image-width='wide'] {
                grid-column: 7/-1;
                & + .card__side-image__text {
                    grid-column: 1/7;
                }
            }
        }
    } // Figure end
}
